@charset "utf-8";

@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;1,400;1,500;1,600&display=swap');

// Define defaults for each variable.

$base-font-family: 'Poppins', Arial, Helvetica, sans-serif !default;
$base-font-size:   16px !default;
$base-font-weight: 400 !default;
$medium-font-weight: 500 !default;
$bold-font-weight: 600 !default;
$small-font-size:  12px !default;
$base-line-height: 1.6 !default;

$spacing-unit:     30px !default;

$text-color:       #222222 !default;
$background-color: #fdfbf7 !default;
$brand-color:      #6c58ed !default;
$brand-color-light: #DAD5FB !default;
$brand-color-dark: #2e207c !default;


$grey-color:       #828282 !default;
$grey-color-light: lighten($grey-color, 40%) !default;
$grey-color-dark:  darken($grey-color, 20%) !default;

$dropshadow-small: drop-shadow(6px 6px 12px #e4e4e4) !default;
$dropshadow: drop-shadow(8px 8px 12px #bebebe) !default;

$round-corners: 8px !default;

$divider: 1px dotted $brand-color !default;

$hover-fade: 0.5s !default;

// Width of the content area
$content-width:    1200px !default;

$on-palm:          500px !default;
$on-laptop:        800px !default;

// Use media queries like this:
// @include media-query($on-palm) {
//   .wrapper {
//     padding-right: $spacing-unit / 2;
//     padding-left: $spacing-unit / 2;
//   }
// }
@mixin media-query($device) {
  @media screen and (max-width: $device) {
    @content;
  }
}

@mixin relative-font-size($ratio) {
  font-size: $base-font-size * $ratio;
}

// Import partials.
@import
  "minima/base",
  "minima/layout",
  "minima/head-foot",
  "minima/aboutme",
  "minima/work",
  "minima/home",
  "minima/syntax-highlighting"
;