/* Page content */

.page-content {
  padding: $spacing-unit 0;
  flex: 1;
}

.page-heading {
  @include relative-font-size(2);
}

.post-list-heading {
  @include relative-font-size(1.75);
}

.post-list {
  margin-left: 0;
  list-style: none;

  > li {
    margin-bottom: $spacing-unit;
  }
}

.post-meta {
  margin-top: $spacing-unit / 2;
  font-size: $small-font-size;
  color: $grey-color;
}

.post-link {
  display: block;
  @include relative-font-size(1.5);
}



/* Posts */
.post-header {
  margin-bottom: $spacing-unit;
}

.post-title {
  @include relative-font-size(2.2);
  font-weight: $medium-font-weight;
  line-height: 1;
  margin-top: $spacing-unit;
  color: $text-color;
}

.post-content {
  margin-bottom: $spacing-unit;

  h2 {
    @include relative-font-size(2);

    @include media-query($on-laptop) {
      @include relative-font-size(1.75);
    }
  }

  h3 {
    color: $text-color;

    @include relative-font-size(1.625);

    @include media-query($on-laptop) {
      @include relative-font-size(1.375);
    }
  }

  h4 {
    @include relative-font-size(1.25);

    @include media-query($on-laptop) {
      @include relative-font-size(1.125);
    }
  }
}

/* project page */

.tag-wrapper {
  margin: $spacing-unit 0;
  display: flex;
  flex-direction: row;
  gap: $spacing-unit / 2;
}

.project-end {
  margin: $spacing-unit * 2 0;
  padding-top: $spacing-unit;
  border-top: $divider;
  text-align: center;
}

@include media-query($on-palm) {
  .tag-wrapper {
      margin-top: $spacing-unit / 2;
      display: flex;
      flex-wrap: wrap;
      gap: 8px;
      width: fit-content;
  }
}

/* 404 */
.error-container {
  margin: 10px auto;
  max-width: 600px;
  text-align: center;
  
  h1 {
    margin: 30px 0;
    font-size: $base-font-size *  2.7;
    line-height: 1;
    letter-spacing: -1px;
    color: $brand-color;
  }

}

